import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Button } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';

class Login extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    state = {
        redirect: false
      };

    componentDidMount() {
    }

    async componentWillReact() {
        if(this.props.store.isAuthenticated) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    onHandleLogin = (event) => {
        console.log("onHandleLogin");
        // let name = event.target.email.value;
        // let password = event.target.password.value;

        // const data = {
        //     email, password
        // };
        // this.props.store.login(data);
        this.setState({ redirect: true });
    }

    render() {
        const { isAuthenticated } = this.props.store;

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/" />;
        }
        else {
            return (
                <Fragment>
                    <h1>Login</h1>
                    <Form>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Логин</Form.Label>
                            <Form.Control name="login" type="text" placeholder="Enter login" />
                        </Form.Group>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Пароль</Form.Label>
                            <Form.Control name="password" type="password" placeholder="Password" />
                        </Form.Group>
                        <Button variant="primary" type="submit" onClick={this.onHandleLogin}>
                            Войти
  </Button>
                    </Form>
                </Fragment>
            )
        }

    }
}

Login = observer(Login);

export default withRouter(Login);